import React, { Component } from 'react';
import { Row, Col, Form, Select } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { SelectAjax } from 'components/common';
import { getTrainTypes } from 'services/trainType';
import { getCourses } from 'services/PaperEnrollment';
import { udpateQueryParams, getQueryParams } from 'utils/urlUtil';

const { Option } = Select;
@Form.create({ name: 'FormSearchPaper' })
class FormSearchPaper extends Component {
  state = {
    listCourses: [],
  };

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
    this.getCourses();
  };

  getCourses = async () => {
    const courses = await getCourses();
    this.setState({
      listCourses: courses.data.items,
    });
  };

  formItemLayout = 6;

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      udpateQueryParams(values);
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  render() {
    const { listCourses } = this.state;
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={this.formItemLayout}>
            <Form.Item label="H">
              {getFieldDecorator('trainTypeId')(<SelectAjax service={getTrainTypes} />)}
            </Form.Item>
          </Col>
          <Col span={this.formItemLayout}>
            <Form.Item label="Kha">
              {getFieldDecorator('courseId')(
                <Select>
                  {listCourses.map(item => (
                    <Option key={item.id} value={item.id}>
                      {item.label}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={this.formItemLayout}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchPaper;



