import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { Card, Row, Col, message } from 'antd';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { getPaperEnrollments, deletePaperEnrollment } from 'services/PaperEnrollment';
import { getQueryParams } from 'utils/urlUtil';
import routeConstant from 'routes/route/routeConstant';
import FormSearchPaper from './FormSearchPaper';
import './styles.scss';

class ListTypePaperEnrollmentsPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    console.log(' getQueryParams()', getQueryParams());
    this.setState({ paramSearch: getQueryParams() });
  };

  handleDelete = async id => {
    await deletePaperEnrollment(id);
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };
  render() {
    const { reload } = this.state;
    const columns = [
      {
        align: 'center',
        title: 'Kha',
        dataIndex: 'courseName',
        key: 'courseName',
      },
      {
        width: '50px',
        align: 'center',
        title: 'K hiu',
        dataIndex: 'id',
        key: 'id',
      },
      {
        width: '30%',
        title: 'Tn giy t',
        dataIndex: 'paperTypeName',
        key: 'paperTypeName',
      },
      {
        width: '35px',
        align: 'right',
        title: 'S lng',
        dataIndex: 'noOfPieces',
        key: 'noOfPieces',
      },
      // {
      //   width: '75px',
      //   align: 'center',
      //   title: 'Bt buc',
      //   render: (text, record) => <Checkbox checked={record.isMandantory} />,
      // },
      {
        width: '50px',
        align: 'center',
        title: 'STT',
        dataIndex: 'orderNo',
        key: 'orderNo',
      },
      {
        title: 'Ghi ch',
        dataIndex: 'note',
        key: 'note',
      },
      {
        width: '100px',
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname:
                  routeConstant.TUYEN_SINH_NHAP_HOC__QUYET_DINH_LOAI_GIAY_TO_NHAP_HOC__NHAP_DU_LIEU,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card className="type-of-paper" title="Q loi giy t">
        <Row>
          <Col span={24}>
            <CartHeadAction>
              <Link
                to={
                  routeConstant.TUYEN_SINH_NHAP_HOC__QUYET_DINH_LOAI_GIAY_TO_NHAP_HOC__NHAP_DU_LIEU
                }
              >
                <BtnAddNew />
              </Link>
            </CartHeadAction>
          </Col>
        </Row>
        <FormSearchPaper />
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              formSearch
              paramSearch={this.state.paramSearch}
              service={getPaperEnrollments}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListTypePaperEnrollmentsPage />
  </Page>
);



